
body{
    background-color:#eee5e9;

}
.xyz{
    margin: 80px;
    display: grid;
    grid-template-columns : repeat(1,1fr);
    gap: 20px;
   align-items: center;
}
.tit{
    padding-top: 3%;
  
}
.Service{
    color: rgb(20, 220, 193);
    padding:2%;
    text-decoration: underline;
}
.xyz > article{
    background-color: #eee5e9;
    border: none;
    box-shadow: 2px  2px 6px rgba(0,0,0,0.3);
    border-radius: 20px;
    text-align: center;
    width:100;
    transition: transfom .3s;
}
p{
   
    text-align: left;
    margin-left:15px;
    font-size: 20px;
    margin-right:15px;
}
.city p {
    text-align: center;
    font-family: fantasy;
    padding-bottom: 2%;
}

button {
    padding: 1.5%;
    background-color: rgb(139, 223, 238);
    box-shadow: 2px  2px 6px rgba(0,0,0,0.3);
    border-radius: 5px;
    cursor: pointer;
    text-decoration: none;
    border: none;
    margin-left: 2%;
   float: left;
   font-size: 20px;
}

button:hover{
    background-color: rgb(201, 231, 236);
    transform: translateY(5px);
    box-shadow: 2px  2px 26px rgba(0,0,0,0.3);  
}
.xyz > article:hover{
    transform: translateY(5px);
    box-shadow: 2px  2px 26px rgba(0,0,0,0.3); 
}
.inline {
    background-color: white;
}
.menu h1{
    text-align: center;
}
.iline div{
    display: inline-block;
}
.text img{
    margin:2%;
    width:35%;
    border-radius: 10%;
}

.text a{
    text-decoration: none;
    padding: 2% 16% 2% 16%;
margin-top:20%;
    background-color: #cb202d;
    color: whitesmoke;
    border-radius: 10px;
}
h2 span{
    font-size: 125%;
}
.text a:hover{
   

    background-color: #20c0cb;
    color: whitesmoke;
    
    transform: translateY(5px);
    box-shadow: 2px  2px 26px rgba(0,0,0,0.3); 
}
.sbox{
    top:0;
    left: 0;
    width: 25%;
    height:35px;
    padding:10px 20px;
    border-radius: 50px;
    font-size: 18px;  
   border: none;
    text-decoration:none;
}

.heading{
    background-color: #cb202d;
    color: #fff;
    margin-bottom: 30px;
    padding: 30px 0;
    grid-column: 1/-1;
    text-align: center;
}

.heading >h1{
    font-weight: 400;
    font-size: 30px;
    letter-spacing: 10px;
    margin-bottom: 10px;
}

.heading >h3{
    font-weight: 600;
    font-size: 22px;
    letter-spacing: 5px;
}